<template>
    <div class="content container">
        <h1 class="text-center">ChangeLog</h1>
        <section v-for="cl in changeLogs">
            <h3>v{{cl.version}} <small>{{cl.time}}</small></h3>
            <p v-for="log in cl.changes" v-html="log"></p>
        </section>
    </div>
</template>

<script>
    export default {
        name: "ChangeLog",
        data(){
            let st = this.$store.state, cl;
            try{
                cl = st.changeLog[st.currentProject][st.language];
            }catch (e) {
                cl = [];
            }

            return {
                changeLogs: cl
            };
        },
        mounted(){
            if(!this.$store.state.currentProject) this.$router.push({path: '/'});
        }
    }
</script>

<style lang="scss" scoped>
    .content{
        background-color: white;
        width: 800px;
        padding-bottom: 30px;
        .jumbotron{
            background-color: white;
            padding-left: 0;
            margin-bottom: 10px;
            h1 {
                font-size: 50px;
                font-weight: lighter;
            }
        }
        h3{
            color: white;
            background-color: #41B883;
            border-radius: 3px;
            padding: 3px 5px;
            display: inline-block;
            small{
                color: green;
            }
        }
        .row h3 i{ }
        .desc{
            font-size: 15px;
            color: #666666;
            margin: 0 50px 20px 50px;
        }
        p{
            margin: 0 0 5px 60px;
            font-size: 16px;
            display: list-item;
            list-style: disc;
        }
    }
</style>


